import { parsePath } from '../utils';
import { pushTarget, popTarget } from './dep';
import { queueWatcher } from './scheduler';

let uid = 0;

export default class Watcher {
  constructor(vm, expOrFn, options) {
    this.vm = vm;
    vm._watchers.push(this);

    // options æºç ä¸­è¿ææ´å¤ï¼æ¯å¦ï¼deep
    if (options) {
      this.lazy = !!options.lazy;
    } else {
      this.lazy = false;
    }

    this.id = ++uid;
    this.deps = []; // å­å¨åå½å watcher ç¸å³çååºå¼å±æ§ç dep
    this.newDeps = []; // æ°ç deps
    this.depIds = new Set();
    this.newDepIds = new Set();

    if (typeof expOrFn === 'function') {
      this.getter = expOrFn;
    } else {
      // parsePath è§£æå±æ§(a.b.c)å¯¹åºå¨ vm ç valueï¼æºç å­å¨ä½æ¬å¤ä¸æ¶åå°
      this.getter = parsePath(expOrFn);
      if (!this.getter) {
        this.getter = function () {};
        process.env.NODE_ENV !== 'production' &&
          console.warn(
            `Failed watching path: "${expOrFn}" ` +
              'Watcher only accepts simple dot-delimited paths. ' +
              'For full control, use a function instead.'
          );
      }
    }

    // åæ¬¡æ§è¡
    this.value = this.lazy ? undefined : this.get();
  }

  get() {
    // è¿å°±æ¯è®© Dep.target ä¿å­å½åç Watcher
    pushTarget(this);
    try {
      this.getter(); // æ§è¡æ´æ°å½æ°
    } catch (e) {
      throw e;
    } finally {
      popTarget(); // å¼¹åº Dep.Target
      this.cleanupDeps(); // å Watcher ç dep æ¸é¤
    }
  }

  addDep(dep) {
    const id = dep.id;
    // å½å Watcher çæ° depIds ä¸å­å¨ï¼å°±å å¥ä¼ è¿æ¥ç dep
    if (!this.newDepIds.has(id)) {
      this.newDepIds.add(id);
      this.newDeps.push(dep); // Watcher å³èç dep ä¿å­
      // Watcher å³èç dep æ²¡æä¿å­è¿ï¼è®© dep æ¶éå½åç Watcher ä¾èµ
      // Watcher å·²ä¿å­è¿äº dep é£å°±è¯´æï¼å½å dep ä¹å·²ç»æ¶éäºå½å Watcher äºï¼ææå°±ä¸éè¦è®© dep æ¶éä¾èµäº
      if (!this.depIds.has(id)) {
        dep.addSub(this);
      }
    }
  }

  /**
   * Clean up for dependency collection.
   */
  cleanupDeps() {
    // æºç ææ´å¤
    this.depIds = this.newDepIds;
    this.newDepIds.clear(); // æ¸é¤ææ°è®°å½ depIds éå
    this.deps = this.newDeps;
    this.newDeps.length = 0; // æ¸é¤ææ°è®°å½ dep éå
  }

  update() {
    // è¿éçè¯ï¼æºç ä¸­ä¼ææ´å¤çå¤æ­
    // ä¸è¬æ¥è¯´æ´æ°çè¯ï¼ä¼è¿è¡å¼æ­¥æ´æ°ï¼ä¼æ¶åå° nextTick å½æ°
    // æç»ä¼æ§è¡ run() å½æ°
    queueWatcher(this);
  }

  run() {
    this.get();
  }
}
