import Dep from './dep';
import { VNode } from '../vdom/vnode';
import { arrayMethods } from './array';
import { isArray, isObject, def, hasProto } from '../utils';

const arrayKeys = Object.getOwnPropertyNames(arrayMethods);

class Observer {
  constructor(data) {
    this.dep = new Dep();
    def(data, '__ob__', this);

    if (isArray(data)) {
      const augment = hasProto ? protoAugment : copyAugment;
      augment(data, arrayMethods, arrayKeys);
      this.observeArray(data);
    } else {
      this.walk(data);
    }
  }

  walk(data) {
    const keys = Object.keys(data);
    keys.forEach(function (key) {
      defineReactive(data, key, data[key]);
    });
  }

  observeArray(data) {
    data.forEach(function (item) {
      observe(item);
    });
  }
}

function protoAugment(target, src, keys) {
  target.__proto__ = src;
}

function copyAugment(target, src, keys) {
  for (let i = 0, l = keys.length; i < l; i++) {
    const key = keys[i];
    def(target, key, src[key]);
  }
}

function defineReactive(data, key, val) {
  const dep = new Dep(); // é­åä¸ä¸ªä¾èµå¯¹è±¡

  // æ¥æ¾å±æ§çæè¿°å¨
  const property = Object.getOwnPropertyDescriptor(data, key);
  if (property && property.configurable === false) {
    return;
  }

  // å¤æ­æ¯å¦å±æ§æ¬èº«è®¾ç½®è¿å­å¨å¨æè¿°
  const getter = property && property.get;
  const setter = property && property.set;

  let childOb = observe(val); // å¯¹å¼åé¨çå±æ§è¿è¡æ°æ®å«æï¼è¿éæºç ä¸­ä¼åä¸ä¸ª shallow å¤æ­ï¼æ¯å¦ä¸å¼å§æ·±å±è¿è¡ä¾èµ

  Object.defineProperty(data, key, {
    enumerable: true,
    configurable: true,
    get() {
      const value = getter ? getter.call(data) : val;
      if (Dep.target) {
        dep.depend(); // æ¶éä¾èµ,åæ¬ Watcher æ¶é dep å dep æ¶é Watcher
        if (childOb) {
          /**
           * å­æ°æ®ååºå¯¹è±¡æ¬èº«ç this.dep ä¹è¿è¡ä¾èµæ¶é
           * è¿éå¶å®å¯¹äºæ°ç»æ¹æ³éåçå«æè§¦åæ´æ°ï¼å°±æ¯è°ç¨è¿éç this.dep
           * {a: [1, 2]}
           * a ä¼é­åä¸ä¸ª dep ä¾èµï¼ç¶å valueï¼[1, 2] ä¼æå»ºåº ob ç¶åå¯¹ ob.dep ä¹ä¼æ¶éå½åçä¾èµ
           */
          childOb.dep.depend();
          if (Array.isArray(value)) {
            // å¦ææ¯æ°ç»ï¼å¯¹éé¢æ¯ä¸ä¸ªååºå¯¹è±¡æ¬èº«ç this.dep ä¹è¿è¡ä¾èµæ¶é
            dependArray(value);
          }
        }
      }
      // console.log('ååºå¼è·åï¼' + value);
      return value;
    },
    set(newVal) {
      const value = getter ? getter.call(newVal) : val;
      // è¿éç newVal !== newVal && value !== value ä¸ºäºé¿å newVal å value æ¯ä¸ä¸ª NaNï¼å¾ä¸¥è°¨
      if (newVal === value || (newVal !== newVal && value !== value)) {
        return;
      }
      // console.log('ååºå¼è®¾ç½®ï¼' + key + ' = ' + newVal);
      if (setter) {
        setter.call(data, newVal);
      } else {
        val = newVal;
      }
      observe(newVal); // å¯¹å¼åé¨çå±æ§è¿è¡æ°æ®å«æï¼è¿éæºç ä¸­ä¼åä¸ä¸ª shallow å¤æ­ï¼æ¯å¦ä¸å¼å§æ·±å±è¿è¡ä¾èµ
      dep.notify(); // è§¦åä¾èµæ´æ°å½æ°
    },
  });
}

function dependArray(value) {
  for (let e, i = 0, l = value.length; i < l; i++) {
    e = value[i];
    e && e.__ob__ && e.__ob__.dep.depend();
    if (Array.isArray(e)) {
      dependArray(e);
    }
  }
}

function observe(data) {
  if (!isObject(data) || data instanceof VNode) {
    return;
  }
  let ob;
  if (data.__ob__) {
    ob = data.__ob__;
  } else {
    ob = new Observer(data);
  }
  return ob;
}

export { observe };
