import { nextTick } from '../utils';

const queue = [];
let has = {};
let waiting = false;
let flushing = false;
let index = 0;

// å®æå¼æ­¥ä»»å¡ï¼å°éåæ¸çå®æ¯åï¼éç½®
function resetSchedulerState() {
  index = queue.length = 0;
  has = {};
  waiting = flushing = false;
}

function flushSchedulerQueue() {
  let watcher, id;

  queue.sort((a, b) => a.id - b.id);

  for (index = 0; index < queue.length; index++) {
    watcher = queue[index];
    id = watcher.id;
    has[id] = null;
    watcher.run();
  }
  resetSchedulerState();
}

function queueWatcher(watcher) {
  const id = watcher.id;
  // undefined == null -> true
  if (has[id] == null) {
    has[id] = true;
    // å¤æ­å¼æ­¥ä»»å¡æ¯å¦å¼å§æ§è¡ï¼å¦ææªæ§è¡ç´æ¥æ¾å°éååé¢
    if (!flushing) {
      queue.push(watcher);
    } else {
      // å¦æå·²ç»å¼å§æ§è¡éåä»»å¡äº,å°å½å watcher æ¾å¥å°éåæåºç¸åºä½ç½®ï¼å å¥æ§è¡éåä¸­
      let i = queue.length - 1;
      while (i > index && queue[i].id > watcher.id) {
        i--;
      }
      queue.splice(i + 1, 0, watcher);
    }
    // åå§åãå¼æ­¥éåä¸­å·²å­å¨ watcherãå¼æ­¥ä»»å¡æ§è¡ä¸­ä¸æªæ§è¡å®çç¶æä¸ï¼ä¸åè®¸å¼éä¸ä¸ä¸ªå¼æ­¥ä»»å¡
    if (!waiting) {
      waiting = true;
      nextTick(flushSchedulerQueue);
    }
  }
}

export { queueWatcher };
