import { isUndef, isDef } from '../../shared/util.js';
import { diff } from './diff.js';
import { doPatch } from './doPatch.js';
import { createElement, removeElement } from './rnode.js';

function patch(oldVnode, vnode) {
  // å¤æ­æ°ç vnode ä¸å­å¨
  if (isUndef(vnode)) {
    // å¤æ­æ§èç¹å­å¨
    if (isDef(oldVnode)) {
      // Vue æºç è¿éä¼æ§è¡éæ¯ççå½å¨æå½æ°ï¼ç´æ¥å é¤æ§èç¹åå®¹
      removeElement(oldVnode);
    }
    return;
  }

  // å¤æ­ oldVnode æ¯å¦æ¯çå®èç¹
  const isRealElement = isDef(oldVnode.nodeType),
    el = isRealElement ? oldVnode : oldVnode.el;

  if (isRealElement) {
    // é¦æ¬¡æè½½
    const nEl = createElement(vnode),
      parentElement = el.parentElement;

    // el.nextSibling æçæ¯ el çåå¼èç¹
    parentElement.insertBefore(nEl, el.nextSibling);
    parentElement.removeChild(el);
    return nEl;
  } else {
    // oldVnode ä¸ vnode diff æ¯è¾æè¡¥ä¸
    const patches = diff(oldVnode, vnode);
    console.log('patches', patches);
    if (Object.keys(patches).length) {
      // å¼å§æè¡¥ä¸
      doPatch(el, patches);
    }
    vnode.el = el;
    return el;
  }
}

export { patch };
