import { def } from '../utils';

const originArrMethods = Array.prototype,
  arrayMethods = Object.create(originArrMethods); // åå»ºä¸ä¸ªå¯¹è±¡ï¼ å¹¶ç»§æ¿ Array.prototype

const ARR_METHODS = [
  'push',
  'pop',
  'shift',
  'unshify',
  'reverse',
  'sort',
  'splice',
];

ARR_METHODS.forEach(function (method) {
  def(arrayMethods, method, function (...args) {
    const result = originArrMethods[method].apply(this, args),
      ob = this.__ob__;

    let newArr;

    switch (method) {
      case 'push':
      case 'unshift':
        newArr = args;
        break;
      case 'splice':
        newArr = args[2];
        break;
      default:
        break;
    }

    // å¯¹æ°ç»æ°å å¥çåç´ è¿è¡æ°æ®å«æ
    if (newArr) ob.observeArray(newArr);
    // notify change
    ob.dep.notify();
    // è¿ååå½æ°æ§è¡ç»æ
    return result;
  });
});

export { arrayMethods };
