const fnExpRE = /^([\w$_]+|\([^)]*?\))\s*=>|^function(?:\s+[\w$]+)?\s*\(/;
const fnInvokeRE = /\([^)]*?\);*$/;
const simplePathRE =
  /^[A-Za-z_$][\w$]*(?:\.[A-Za-z_$][\w$]*|\['[^']*?']|\["[^"]*?"]|\[\d+]|\[[A-Za-z_$][\w$]*])*$/;

function genHandler(handler) {
  if (!handler) {
    return 'function(){}';
  }
  if (Array.isArray(handler)) {
    return `[${handler.map(handler => genHandler(handler)).join(',')}]`;
  }

  const isMethodPath = simplePathRE.test(handler.value); //æ¯å¦ä¸ºç®åçè¡¨è¾¾å¼ï¼æ¯å¦ onShow ç­
  const isFunctionExpression = fnExpRE.test(handler.value); //æ¯å¦ä¸ºå½æ°è¡¨è¾¾å¼(ç®­å¤´å½æ°æfunction(){}æ ¼å¼çå¿åå½æ°)
  const isFunctionInvocation = simplePathRE.test(
    // æ¯å¦éè¦ return
    handler.value.replace(fnInvokeRE, '')
  );

  // æ¯å¦æä¿®é¥°ç¬¦ï¼æ¯å¦ @click.sync
  if (!handler.modifiers) {
    if (isMethodPath || isFunctionExpression) {
      return handler.value;
    }
    return `function($event){${
      isFunctionInvocation ? `return ${handler.value}` : handler.value
    }}`; // inline statement
  }
}

export function genHandlers(events, isNative) {
  const prefix = isNative ? 'nativeOn:' : 'on:';
  let staticHandlers = ``;
  let dynamicHandlers = ``;
  for (const name in events) {
    const handlerCode = genHandler(events[name]);
    if (events[name] && events[name].dynamic) {
      dynamicHandlers += `${name},${handlerCode},`;
    } else {
      staticHandlers += `"${name}":${handlerCode},`;
    }
  }
  staticHandlers = `{${staticHandlers.slice(0, -1)}}`;
  if (dynamicHandlers) {
    return prefix + `_d(${staticHandlers},[${dynamicHandlers.slice(0, -1)}])`;
  } else {
    return prefix + staticHandlers;
  }
}
