import { genHandlers } from './events';

// å¤æ­æ¯å¦ä¸º {{  }} è¿ç§ææ¬
const defaultTagRE = /\{\{((?:.|\r?\n)+?)\}\}/g;

// ä½ç½®ï¼vue/src/compiler/codegen/index.js
function genProps(attrs) {
  let attrStr = '';
  let dynamicAttrStr = '';
  let dynamic = false;

  attrs.forEach(item => {
    dynamic = item.dynamic;
    if (dynamic) {
      // className, name | "name"
      dynamicAttrStr += `${item.name.slice(1)}, ${item.value},`;
    } else {
      // "class": app | "app"
      attrStr += `${JSON.stringify(item.name)}: ${item.value},`;
    }
  });

  attrStr = `{${attrStr.slice(0, -1)}}`;

  if (dynamicAttrStr) {
    return `_d(${attrStr}, [${dynamicAttrStr.slice(0, -1)}])`;
  } else {
    return attrStr;
  }
}

function genData(el) {
  let data = '{';
  if (el.attrs) {
    data += `attrs:${genProps(el.attrs)},`;
  }
  if (el.events) {
    data += `${genHandlers(el.events, false)},`;
  }
  data = data.replace(/,$/, '') + '}';
  return data;
}

function generateChild(node) {
  if (node.type === 1) {
    return generate(node);
  } else if (node.type === 3) {
    let text = node.text;

    if (!defaultTagRE.test(text)) {
      // è¿ééè¦ JSON.stringify å¨åä¸ä¸ï¼å¦èä¼åºç° _v(hello) è§£æçæ¶å hello ä¸å°±æäºåéäº
      return `_v(${JSON.stringify(text)})`;
    }

    let match,
      index, // ä¿å­æ­£åæåè§£æé¡¹çå¼å§ç´¢å¼
      // defaultTagRE.lastIndex ä¼å¨æ­£åè§£æçæ¶åï¼ä¸ç´å¤äºå¹éé¡¹æ«ç«¯çç´¢å¼ï¼ä¸ä¸è½®å¹éå°±ä» defaultTagRE.lastIndex å¼å§
      lastIndex = (defaultTagRE.lastIndex = 0),
      textArr = [];

    while ((match = defaultTagRE.exec(text))) {
      index = match.index; // ä¿å­æ­£åæåè§£æé¡¹çå¼å§ç´¢å¼
      if (index > lastIndex) {
        textArr.push(JSON.stringify(text.slice(lastIndex, index)));
      }
      // è¿éä¸éè¦ JSON.stringify åå æ¯ï¼match[1] æçå°±æ¯ data çå±æ§
      textArr.push(`_s(${match[1].trim()})`);
      lastIndex = index + match[0].length;
    }

    // æ­£åå¹éä¸å°äºï¼ä½æ¯è¿å©ä¸éæææ¬
    if (lastIndex < text.length) {
      textArr.push(JSON.stringify(text.slice(lastIndex)));
    }

    // _v('æçå§åæ¯ï¼'+_s(name)+'ï¼è¯·å¤å¤å³ç§~')
    return `_v(${textArr.join('+')})`;
  }
}

function getChildren(el) {
  const children = el.children;

  if (children) {
    return children.map(c => generateChild(c)).join(',');
  }
}

function generate(el) {
  const children = getChildren(el);

  const code = `_c(${JSON.stringify(el.tag)}, ${
    genData(el)
    // el.attrs.length > 0 ? formatProps(el.attrs) : 'undefined'
  }${children ? `, ${children}` : ''})`;

  return code;
}

export { generate };
