import { remove } from '../utils';

let uid = 0;

export default class Dep {
  constructor() {
    this.id = uid++;
    this.subs = [];
  }

  addSub(sub) {
    this.subs.push(sub);
  }

  removeSub(sub) {
    remove(this.subs, sub);
  }

  depend() {
    // ç»å½å Watcher push ä¾èµï¼ä¸ºäºåç»­å dep ç subs è¿è¡æ¯è¾ï¼ç§»é¤ææ ç¨çä¾èµï¼
    // ææå°±æ¯æ²¡ç¨å°çå±æ§ï¼å¨å±æ§åæ´åå°±ä¸è¦éç¥ç¸å¯¹åºç Watcher äº
    if (Dep.target) {
      Dep.target.addDep(this); // Watcher å¯¹è±¡ä¸­ç addDepï¼éé¢ä¼è°ç¨ dep.addSub()
    }
  }

  notify() {
    // stabilize the subscriber list first
    const subs = this.subs.slice(); // æµæ·è´
    for (let i = 0, l = subs.length; i < l; i++) {
      subs[i].update(); // è§¦åä¾èµæ´æ°å½æ°
    }
  }
}

Dep.target = null;
const targetStack = [];

export function pushTarget(_target) {
  if (Dep.target) targetStack.push(Dep.target);
  Dep.target = _target;
}

export function popTarget() {
  Dep.target = targetStack.pop();
}