import { addAttr, addHandler } from './helpers';
// id="app" id='app' id=app
const attribute =
  /^\s*([^\s"'<>\/=]+)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/;
const dynamicArgAttribute =
  /^\s*((?:v-[\w-]+:|@|:|#)\[[^=]+?\][^\s"'<>\/=]*)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/;
//æ ç­¾å  <my-header></my-header>
const ncname = `[a-zA-Z_][\\-\\.0-9_a-zA-Z]*`;
// <my:header></my:header>
const qnameCapture = `((?:${ncname}\\:)?${ncname})`;
// <div
const startTagOpen = new RegExp(`^<${qnameCapture}`);
// > />
const startTagClose = /^\s*(\/?)>/;
// </div>
const endTag = new RegExp(`^<\\/${qnameCapture}[^>]*>`);

const dynamicArgRE = /^\[.*\]$/;
const dirRE = /^v-|^@|^:|^#/;
const bindRE = /^:|^v-bind:/;
const onRE = /^@|^v-on:/;

// ä»¥ä¸æ­£åæ¯ä»å®æ¹ Vue æºç æ£ç

// src/compiler/parser/index.js æ¹æ³åï¼parse -> parseHTML
function parseHtmlToAst(html) {
  let text,
    root,
    currentParent,
    stack = [];

  while (html) {
    // æ¥æ¾ < å®åæ¬ä¾å¦ï¼ <div> </div>
    let textEnd = html.indexOf('<');

    if (textEnd === 0) {
      // è·åå°æ ç­¾ç»è£å¥½çç»æå¯¹è±¡
      const startTagMatch = parseStartTag();

      // å¯¹è¯¥æ ç­¾è¿è¡å­ç¶å±çº§å¤ç
      if (startTagMatch) {
        start(startTagMatch.tagName, startTagMatch.attrs);
        continue;
      }

      // è·åæ ç­¾ç»æ </div> è¿è¡éç½®
      const endTagMatch = html.match(endTag);

      if (endTagMatch) {
        // å é¤ç»ææ ç­¾çå­ç¬¦
        advance(endTagMatch[0].length);
        // éå°ç»ææ ç­¾ï¼è¿è¡ç¶å­ç®¡çå¤ç
        end();
        continue;
      }
    }

    if (textEnd > 0) {
      text = html.substring(0, textEnd);
    }

    if (text) {
      advance(text.length);
      chars(text);
    }
  }

  // src/compiler/parser/html-parser.js
  function parseStartTag() {
    const start = html.match(startTagOpen);

    let end, attr;

    if (start) {
      // ä¿å­æ ç­¾å
      const match = {
        tagName: start[1],
        attrs: [],
      };
      // å é¤å¹éé¡¹
      advance(start[0].length);

      // å¼å§å¹éæ ç­¾å±æ§
      while (
        !(end = html.match(startTagClose)) &&
        (attr = html.match(dynamicArgAttribute) || html.match(attribute))
      ) {
        match.attrs.push({
          name: attr[1],
          value: attr[3] || attr[4] || attr[5],
        });
        advance(attr[0].length);
      }

      if (end) {
        // å é¤å¹éé¡¹
        advance(end[0].length);
        return match;
      }
    }
  }

  // èç¹å®ç»æ¶ï¼å¤çé¢å¤äºç©
  function closeElement(element) {
    element = processElement(element);
  }

  function processElement(element) {
    // processRef(element);
    // processSlotContent(element);
    // processSlotOutlet(element);
    // processComponent(element);
    processAttrs(element);
    return element;
  }

  function processAttrs(el) {
    const list = el.attrs;
    el.attrs = [];
    let i, l, name, value, isDynamic;
    for (i = 0, l = list.length; i < l; i++) {
      name = list[i].name;
      value = list[i].value;
      // vue æä»¤
      if (dirRE.test(name)) {
        if (bindRE.test(name)) {
          // v-bind
          name = name.replace(bindRE, '');
          isDynamic = dynamicArgRE.test(name);
          if (isDynamic) {
            name = name.slice(1, -1);
          }
          addAttr(el, name, value, isDynamic);
        } else if (onRE.test(name)) {
          // v-on
          name = name.replace(onRE, '');
          isDynamic = dynamicArgRE.test(name);
          if (isDynamic) {
            name = name.slice(1, -1);
          }
          addHandler(el, name, value);
        }
      } else {
        isDynamic = false;
        name = name.replace(dirRE, '');
        if (name === 'style') {
          let styleAttrs = {};
          value.split(';').forEach(subItem => {
            const [key, value] = subItem.split(':');
            styleAttrs[key] = value;
          });
          value = styleAttrs;
        }
        value = JSON.stringify(value);
        addAttr(el, name, value, isDynamic);
      }
    }
  }

  // å¯¹ html å é¤å¹éé¡¹é¿åº¦ç¶å­
  function advance(len) {
    html = html.substring(len);
  }

  // å¯¹å¼å§æ ç­¾è¿è¡ç¶å­å³ç³»å¤ç
  function start(tagName, attrs) {
    const element = createASTElement(tagName, attrs);

    if (!root) {
      root = element;
    }

    stack.push(element);
    currentParent = element;
  }

  // å¯¹ç»ææ ç­¾è¿è¡ç¶å­å³ç³»å¤ç
  function end() {
    // åºæ ï¼åºæ çå°±æ¯å½åå¹éå°çç»ææ ç­¾
    const element = stack.pop();

    // å¤æ­æ¯å¦æç¶çº§
    if (stack.length > 0) {
      // ååºç¶çº§
      currentParent = stack[stack.length - 1];
      // å¤çç¶å­å³ç³»
      element.parent = currentParent;
      currentParent.children.push(element);
    }
    closeElement(element);
  }

  function chars(text) {
    text = text.trim();

    if (text.length > 0) {
      currentParent.children.push({
        type: 3,
        text,
      });
    }
  }

  // ç»è£ AST æ ç­¾å¯¹è±¡ç»æ
  function createASTElement(tagName, attrs) {
    return {
      tag: tagName,
      type: 1,
      children: [],
      attrs,
      parent: Window,
    };
  }

  return root;
}

export { parseHtmlToAst };
